export const mountRoomPositionPrototype = function () {
    _.assign(RoomPosition.prototype, extensions)
}

const extensions = {
    /**
     * Êü•ÁúãËØ•‰ΩçÁΩÆÂë®Âõ¥‰∏ÄÂúàÁöÑÂèØÁî®Âú∞ÂùóÊï∞ÈáèÔºàÈùûÂ¢ôÔºâ
     * @return {number} ÂèØÁî®Âú∞ÂùóÊï∞Èáè
     */
    lookForAvailableAdjacentCount() {
        let count = 0;
        for (let dx = -1; dx <= 1; dx++) {
            for (let dy = -1; dy <= 1; dy++) {
                if (dx === 0 && dy === 0) continue;
                const x = this.x + dx;
                const y = this.y + dy;
                if (x < 0 || x >= 50 || y < 0 || y >= 50) continue;
                const terrain = Game.rooms[this.roomName].getTerrain().get(x, y);
                if (terrain !== TERRAIN_MASK_WALL) {
                    count++;
                }
            }
        }
        return count;
    },

    /**
     * autoplanÁî®ÔºåÊâæÂá∫ÊåáÂÆöËåÉÂõ¥ÂÜÖÂèØ‰ª•ÊîæÁΩÆÂª∫Á≠ëÁöÑÂÖ®ÈÉ®‰ΩçÁΩÆ
     * @param {number} range Êü•ÊâæËåÉÂõ¥(‰ª•this‰∏∫‰∏≠ÂøÉÔºåÂçäÂæÑÂçï‰ΩçÔºåÈªòËÆ§5)
     * @return {[RoomPosition]|null} ÂèØÁî®‰ΩçÁΩÆÔºåÊâæ‰∏çÂà∞ËøîÂõûnull
     */
    findAvailablePosition(range = 5) {
        if (!this.roomName) return null;
        const positions = [];
        for (let dx = -range; dx <= range; dx++) {
            for (let dy = -range; dy <= range; dy++) {
                const x = this.x + dx;
                const y = this.y + dy;
                if (x < 0 || x >= 50 || y < 0 || y >= 50) continue;
                const terrain = Game.rooms[this.roomName].getTerrain().get(x, y);
                if (terrain === TERRAIN_MASK_WALL) continue;
                positions.push(new RoomPosition(x, y, this.roomName));
            }
        }
        return positions.length > 0 ? positions : null;
    }

}